.toolbar{
    position:sticky;
    width:100%;
    height:70px;
    top:0px;
    left:0px;
    z-index:899;
    /* filter:blur(5px); */
    /* backdrop-filter: blur(2px); */
    /* -webkit-filter: blur(5px); */
  }
  .toolbar .toolbar-navigation{
    display:flex;
    align-items:center;
    height:100%;
    padding:10px 25px;
  }
  .toolbar .toolbar-navigation-items ul{
    list-style: none;
    margin:0;
    padding:0;
    display:flex;
  }
  .toolbar .toolbar-navigation-items li{
    padding: 5px 15px;
    transition: 0.3s;
  }
  .toolbar .toolbar-logo{
    margin-left:10px;
  }
  .toolbar .toolbar-logo a{
    text-decoration: none;
  }
  .toolbar .toolbar-navigation-items a{
    text-decoration: none;
    font-size:16px;
    font-weight: 400;
    transition: 0.3s;
  }
  .toolbar .toolbar-navigation-items:hover li{
    opacity:0.5;
  }
  .toolbar .toolbar-navigation-items li:hover{
    opacity:1;
  }

  .spacer{
    flex-grow:1;
  }
  @media(max-width:760px){
    .toolbar-navigation-items{
      display:none;
    }
  }
  @media(min-width:761px){
    .toggle-button{
      display:none;
    }
  }
  @media(max-width:420px){
    .toolbar-logo{
      width:35px;
      height:auto;
    }
    .toggle-button img{
      width:25px;
      height:auto;
    }
  }