
    .side-drawer ul{
    display:flex;
    flex-direction: column;
    list-style: none;
    justify-content: center;
    height: 100%;
    }
    .side-drawer li{
    margin:10px 0px;
    }
    .side-drawer a{
    color:#d9d9d9;
    text-decoration: none;
    transition: 0.3s;
    }
    /* .side-drawer a:hover,
    .side-drawer a:focus{
    color:#9c9c9c;
    } */
    .side-drawer:hover a{
        opacity:0.5;
      }
      .side-drawer a:hover{
        opacity:1;
      }
    .toggle-button{
        outline:none;
        border:none;
        background: none;
    }
    