.main{
    /* background-image: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/41114/plan1.png'); */
    background-size: cover;
    display:flex;
    justify-content: center;
    align-items: center;
    padding:5vh 0vh;
    margin-bottom: 40px;
    filter: grayscale(100%);
    -webkit-filter: grayscale(100%);
}
.frame {
    /* background-image: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/41114/plan1.png'); */
    border-radius: 8px;
    box-shadow: 0px 0px 21px 5px rgba(0,0,0,0.18);
    -webkit-box-shadow: 0px 0px 21px 5px rgba(0,0,0,0.18);
    -moz-box-shadow: 0px 0px 21px 5px rgba(0,0,0,0.18);
    height:59.95vh;
    overflow: hidden;
    position: relative;
    left:50%;
    /* top:100px; */
    transform:translateX(-50%);
    width:79.9vh;
}
.frame img {
    position: absolute;
    transform-origin: 50%;
    width: 100%;
    transition: 2s ease;
    filter: grayscale(100%);
    -webkit-filter: grayscale(100%);
}
.frame:hover .pic1 {
    transform: scale(1.2);
    -webkit-filter: blur(4px);
}
.frame:hover .pic2 {
    transform: scale(1.4);
    -webkit-filter: blur(2px);
}
.frame:hover .pic3 {
    transform: scale(1.6);
    -webkit-filter: blur(1px);
}
.frame:hover .pic4 {
    transform: scale(1.8);
}
.frame:hover .pic5 {
    transform: scale(1.4);
}
.frame:hover .mist {
    transform: translateX(-100%);
}
.frame:hover .mist2 {
    transform: translateX(100%);
}
.mist {
    height: 200px;
    position: absolute;
    top: 500px;
    width: 2000px;
    -webkit-filter: blur(2px);
    transition: 60s linear;
}
.mist2 {
    height: 200px;
    position: absolute;
    top: 300px;
    left: -100%;
    width: 2000px;
    -webkit-filter: blur(2px);
    transition: 60s linear;
}
.about-div{
    height:40vh;
    display:flex;
    justify-content: center;
    align-items: center;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.special-div{
    width:100%;
}
.special-text{
    color:#797979;
    opacity:0.2;
    font-size:90px;
    word-spacing: -10px;
    letter-spacing: -3px;
    text-align: center;
    z-index:9;
    position: relative;
}
.special-paragraph{
    color:#797979;
    font-size:24px;
    font-weight: 300;
    text-align: center;
}
@media(max-width:420px){
    .main{
        margin-bottom:20px;
    }
    .frame{
        width:100%;
        height: 30vh;
    }
    .about-div{
        height:25vh;
    }
    .special-text{
        font-size:35px;
        word-spacing: -7px;
        letter-spacing: 0px;
    }
    .special-paragraph{
        font-size:14px;
    }
}
