.loader{
    display:flex;
    justify-content: center;
    align-items: center;
    width:100%;
    height:100vh;
    z-index:2000;
    position: fixed;
    margin-top:-70px;
}
.loader .circle-of-loader2{
    background-color:#00c680;
    border-radius:50%;
    width:30vh;
    height:30vh;
    display:flex;
    justify-content: center;
    align-items: center;
}
.loader .circle-of-loader{
    background-color:#00c680;
    border-radius:50%;
    position: absolute;
    width:20vh;
    height:20vh;
    display:flex;
    justify-content: center;
    align-items: center;
}
.loader .circle-of-loader img{
  width:60%;
}
.loader .ping {
	-webkit-animation: ping 1.8s ease-in-out infinite both;
	        animation: ping 1.8s ease-in-out infinite both;
}
