.checkmark-bigger-box{
    display: flex;
    justify-content: center;
    align-items: center;
    width:100%;
    height:100vh;
  }
  .checkmark-box{
    background: #090909;
    border:1px solid #30363d;
    border-radius: 4px;
    width:40%;
    height:auto;
    text-align: center;
    padding:30px;
  }
  .heartbeat-text{
    color:#fff;
  }
  .button-crud{
    background-color: #00bc7e;
    border-radius:3px;
    width:30%;
    padding:20px 10px;
    color:white;
    border:none;
    outline:none;
    font-size:16px;
  }