.contact{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.flex-start{
    display:flex;
    align-items: flex-start;
    padding:0px 5px;
}
.button-submit{
    background: #00bc7e;
    border: none;
    outline: none;
    border-radius: 2px;
    text-align: center;
    font-size: 18px;
    font-weight: 400;
    color: #131313;
    padding: 20px 40px;
    transition: 0.3s;
    
}
#form-overlay {
    z-index: 2;
    display: none;
    position: fixed;
    background: rgba(0,0,0,0.5);
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;}
  .loader{
    width:100%;
    height:100vh;
    display:flex;
    justify-content: center;
    align-items: center;
  }
