.about-image{
    margin:5vh 0vh;
    text-align: center;
}
.support-main{
    text-align: center;
    background-color:#00c680;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    height:40vh;
    width:40vh;
}
.support-image{
    width:40%;
    display: inline-block;
}
.support-text{    
    margin-top:5vh;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
}
.support-text-main{
    text-align: left;
    font-size:25px;
    font-weight: 400;
}
.pateron-button{
    background:#ff424d;
    border:none;
    outline:none;
    text-align: center;
    border-radius: 8px;
    font-size:15px;
    color:#fff;
    font-weight: 400;
    margin-right:auto;
    transition: 0.3s;
    margin-top:40px;
    padding:20px 70px;
}
.pateron-button:hover{
    background-color: rgb(249, 46, 58);
}

.about-paragraph{
    font-size:18px;
    font-weight: 300;
    margin:5vh 0vh;
    text-align: left;
}
.container-padding{
    margin-bottom:10vh;
}
.goals{
    text-align: center;
    opacity:0.3;
}
/* .support-box{
    border-color: #30363D;
    border-radius: 8px;
    display:block;
} */
.inspiration{
    margin-bottom:5vh;
}
.friends-image{
    margin-bottom: 5vh;
}
@media(min-width:421px) and (max-width:1080px){
    .about-paragraph{
        font-size:18px;
        margin-top:15vh;
    }
    .support-image{
        height:90%;
    }
}
@media(max-width:420px){
    .about-paragraph{
        font-size:16px;
        margin-top:0vh;
    }
    .support-image{
        height:50%;
    }
    .support-text-main{
        font-size:14px;
        font-weight: 300;
        text-align: center;
    }
    .pateron-button{
        padding:15px 60px;
        margin-top:30px;
        font-size: 13px;
    }
    .support-text{
        align-items: center;
    }
}