.questions{
    margin: 10vh 0vh;
}
.questions .question-box{
    border:1px solid #34353c;
    padding:30px;
    margin-bottom: 30px;
    border-radius: 5px;
}
.questions .question-box .question{
    font-size:16px;
    text-align: left;
    font-weight:600;
    margin-bottom:15px;
}
.questions .question-box .answer{
    font-size:16px;
    text-align: left;
    font-weight:300;
    line-height: 30px;
}
.questions .heading-question{
    font-size:40px;
    font-weight: 700;
    text-align: left;
    margin:70px 0px 40px 0px;
}
.questions .heading-small{
    font-size:16px;
    text-align: left;
    font-weight:300;
    line-height: 30px;
    margin:70px 0px 40px 0px;
}