.section-for-design{
    margin:10vh 0vh 0vh 0vh;
    display:flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .section-for-design-heading{
    font-size:70px;
    font-weight:800;
    text-align: center;
  }
  .section-for-design-heading-small{
    font-size:40px;
    text-align: center;
  }

  @media(max-width:420px){
    .section-for-design-heading{
      font-size:30px;
    }
    .section-for-design-heading-small{
      font-size:25px;
    }
  }