.small-heading{
    margin-top:10vh;
    width: 100%;
    text-align: center;
    font-size:90px;
    font-weight: 400;
    margin-bottom: .5rem;
}
.small-sub-heading{
    margin-bottom:10vh;
    width: 100%;
    text-align: center;
    font-size:18px;
    font-weight: 200;
}
@media(min-width:421px) and (max-width:1080px){
    .small-heading{
        font-size:60px;
    }
    .small-sub-heading{
        font-size:16px;
    }
}
@media(max-width:420px){
    .small-heading{
        font-size:30px;
        font-weight: 600;
    }
    .small-sub-heading{
        font-size:14px;
    }
}