.button-main{
 background: #00bc7e;
 border:none;
 outline:none;
 border-radius: 2px;
 text-align: center;
 font-size:18px;
 font-weight: 400;
 color:#131313;
 padding:20px 40px;
 transition:0.3s;
 margin-top:50px;
}
.button-main:hover{
    padding:20px 70px;
}
@media(max-width:420px){
    .button-main{
        padding:14px 28px;
        font-size: 14px;
        margin-top:30px;
    }
    .button-main:hover{
        padding:14px 38px;
    }
}