.blogs-preview{
    border:1px solid #818181;
    padding:30px;
    border-radius:5px;
    margin-bottom:20px;
    transition: 0.3s;
}
.blogs-preview .blogs-preview-heading{
    text-align: left;
}
.blogs-preview .blog-date{
    margin-bottom:0px;
    opacity: 0.4;
}
.blogs-preview:hover{
    box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.25);
    -webkit-box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.25);
    -moz-box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.25);
}

.blog-detail{
    margin:20vh 0px 5vh 0vh;
}
.blog-detail .blog-detail-heading{
    text-align: center;
    font-size:50px;
    font-weight: 600;
}
.blog-detail .blog-detail-description{
    text-align: center;
    margin:30px 0px 20vh 0px;
}
.blog-detail .blog-box{
    margin-bottom:60px;
    display:flex;
    justify-content: flex-start;
    flex-direction: column;
}
.blog-detail .blog-box .blog-box-heading{
    font-size:24px;
    margin-bottom:15px;
    text-align: left;
    font-weight: 500;
}
.blog-detail .blog-box .blog-box-description{
    font-size:16px;
    font-weight: 300;
    margin-bottom:15px;
    text-align: left;
}
.blog-detail .blog-box .blog-box-link{
    font-size:16px;
    font-weight: 300;
    text-decoration: underline;
    margin-bottom:15px;
    text-align: left;
}