.container-special{
    height:100%;
    max-width: 100%;
    margin-top:-70px;
  }
  .no-container{
    margin-top: 0;
    max-width:initial;
    height:auto;
  }
  .section-for-description{
    margin:15vh 0vh;
  }
  .section-for-description .section-for-description-heading{
    font-size:50px;
    font-weight: 300;
    text-align: left;
  }
  .section-for-description .section-for-description-text{
    font-size:20px;
    margin-top:20px;
    text-align: left;
    line-height:35px;
    font-weight: 300;
  }
  .ipad-one{
    z-index:9;
    position: relative;
    width:100%;
  }
  .ipad-two{
    width:100%;
    z-index:10;
    position: relative;
    margin-top:-17vh;
  }
  .phone-one{
    width:80%;
    margin-top:17vh;
  }
  .phone-two{
    width:80%;
  }
  .visual-design{
    width:100%;
    height:auto;
    margin-bottom:100px;
  }
  .visual-design-1{
    width:100%;
    height:50%;
    padding-bottom:8px;
  }
  .visual-design-2{
    width:auto;
    height:100%;
  }
  .visual-design-3{
    width:100%;
    height:50%;
    padding-top:8px;
  }
  .image-center{
    text-align: center;
    padding:10vh 0vh;
  }
  .technology-design{
    width:auto;
    height: 100%;
  }
  
  @media(max-width:420px){
    .visual-design-2{
      margin-top:14px;
    }
    .visual-design{
      margin-bottom: 0px;
    }
    .section-for-description{
      margin:10vh 0vh;
    }
    .section-for-description .section-for-description-heading{
      font-size:25px;
      font-weight:400;
    }
    .section-for-description .section-for-description-text{
      font-size:15px;
    }
    .ipad-two{
      margin-top:-10vh;
    }
    .image-center{
      padding:5vh 0vh;
    }
    .technology-design{
      width:50%;
    }
    .phone-one{
      margin-top:10vh;
    }
  }