    -webkit-transform: scale(0.95);
    -ms-transition: all 400ms ease-in;
    -ms-transform: scale(0.95);   
    -moz-transition: all 400ms ease-in;
    -moz-transform: scale(0.95);
    transition: all 400ms ease-in;
    transform: scale(0.95);
}
