.section-heading{
    font-size:80px;
    margin-top:20vh;
    color:#bbb;
    text-align: center;
    display: inline-block;
    font-weight: 500;
    width:100%;
}
.section-heading-span{
    font-size: 80px;
    color:#00bc7e;
    display: inline-block;
}
@media(min-width:421px) and (max-width:1080px){
    .section-heading{
        font-size:50px;
        margin-top:15vh;
    }
    .section-heading-span{
        font-size:50px;
    }
}
@media(max-width:420px){
    .section-heading{
        font-size:40px;
        margin-top:10vh;
    }
    .section-heading-span{
        font-size:40px;
    }
}