.footer-box{
    background-color:rgba(121, 121, 121, 0.1);
    display: flex;
    justify-content: center;
    align-items: center;
    padding:10px;
    text-align: center;
    flex-direction: column;
    margin-top:10vh;
}
.footer-box .connect{
    color:#00bc7e;
    font-size:85px;
    padding-top:10vh;
    display:inline-block;
    text-align: center;
    margin-bottom:50px;
}
.footer-box .connect-span{
    color:#BBBBBB;
    display:inline-block;
}
.footer-box .footer-links{
    display: flex;
    justify-content: space-between;
    width:100%;
    margin-top:30px;
    padding-bottom:10vh;
}
.footer-box .footer-links .link{
    font-size:20px;
    color:#BBBBBB;
    text-align: center;
    font-weight: 300;
    transition:0.3s;
    cursor: pointer;
}
.footer-box .footer-links .link:hover{
    color:#8a8a8a;
}
@media(max-width:420px){
    .footer-box .connect{
        font-size:30px;
        margin-bottom:30px;
        padding-top:5vh;
    }
    .footer-box .footer-links{
        display: flex;
        flex-direction: column;        
        padding-bottom:5vh;
        margin-top:0px;
    }
    .footer-box .footer-links .link{
        font-size:14px;
        text-align: center;
        margin-top:20px;
    }
}
